<template>
  <div class="swiper">
  <el-carousel :interval="3000" arrow="never" height="250px">
    <el-carousel-item v-for="item in showTop" :key="item" >
      <h3 >{{item.title}}</h3>
      <img :src="item.image" class="topimage">

    </el-carousel-item>
  </el-carousel>

</div>
</template>

<script>
export default {
  data(){
    return{
      topThemes:[]
    }
  },
  methods:{

  },
  computed:{
    showTop(){
       return this.$store.state.topThemes;
         }


  },
  mounted(){

  },

}

</script>

<style>
.swiper{
top:40px;

}
.el-carousel__item h3 {
  color: #fffafa;
  font-size: 20px;
  opacity: 0.75;
  position:absolute;
  bottom: 1px;
  left: 50px;

}
.topimage{
  width:100%;
}

</style>



